import { Formik, Form, Field, ErrorMessage } from "formik"

const Basic = () => {
  const submitHandler = (values, { setSubmitting }) => {
    // fetch("https://httpbin.org/post", {
    fetch(
      "https://script.google.com/macros/s/AKfycbyv3s6j72cO-825phKIKZ53L20C4LLe_vxXh31RvQqSA8eYh0df/exec",
      // "/googleSpreadsheet",
      {
        method: "POST",
        // mode: "no-cors",
        headers: {
          "Access-Control-Allow-Origin": "*",
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify(values),
      }
    )
      .then(response => console.log("Success!", response))
      .catch(error => console.error("Error!", error.message))

    // setTimeout(() => {
    //   alert(JSON.stringify(values, null, 2))
    //   setSubmitting(false)
    // }, 400)
  }

  return (
    <Layout>
      <SEO title="Page two" />
      <h1>Any place in your app!</h1>
      <Formik
        initialValues={{
          name: "",
          surname: "",
          lastName: "",
          birthDate: "",
          dni: "",
          email: "",
          mobilePhone: "",
          ishoreUser: "",
          federationNumber: "",
        }}
        validate={values => {
          const errors = {}
          if (!values.email) {
            errors.email = "Required"
          } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
          ) {
            errors.email = "Invalid email address"
          }
          return errors
        }}
        onSubmit={submitHandler}
      >
        {({ isSubmitting }) => (
          <Form>
            <FormField name="name" text="Nombre" />
            <FormField name="surname" text="Primer Apellido" />
            <FormField name="lastName" text="Segundo Apellido" />
            <FormField name="birthDate" text="Fecha de nacimiento" />
            <FormField name="dni" text="DNI" />
            <FormField name="email" text="Email" />
            {/* <div>
              <label htmlFor="email">Email</label>
              <Field type="email" name="email" value="1@1.es" />
              <ErrorMessage name="email" component="div" />
            </div> */}
            <FormField name="mobilePhone" text="Móvil" />
            <hr />

            <FormField
              name="inshoreUser"
              text="Usuario de Virtual Regatta Inshore"
            />
            <FormField name="federationNumber" text="Licencia Federativa" />
            {/* Comunidad autónoma */}
            <button type="submit" disabled={isSubmitting}>
              Submit
            </button>
          </Form>
        )}
      </Formik>
      <Link to="/">Go back to the homepage</Link>
    </Layout>
  )
}

export default Basic

const FormField = ({ name, text }) => {
  return (
    <div>
      <label htmlFor={name}>{text}</label>
      <Field type={name} name={name} />
      <ErrorMessage name={name} component="div" />
    </div>
  )
}
